html {
  height:100%;  
}

body {
  margin: 0; 
  max-width: 100%;
  min-height: 100%;
}

#root {
  max-width: 100%;
  min-height: 100%;
  margin: 0;
  overflow: hidden;
}


@font-face {
  font-family:Kumbhfont;
  src: url(/e-commerce-product/static/media/KumbhSansFont.6a289d11.ttf);
}

.App {
  max-width: 100%;
  min-height: 100%;
  margin:0;
  overflow: auto;
  overflow-x: hidden;
  font-family: Kumbhfont;
  font-weight: 400;
  display: grid;
  grid-template-columns: auto;
  grid-template-rows:  auto auto auto ;
}

 
h5 {
  color:hsl(26, 100%, 55%)
}

p {
  font-size: 16px;
  color:hsl(220, 14%, 75%)
}

.App #App-header {
  display: grid;
  grid-template-columns: auto auto;
  padding:15px;
  max-width: 100%;
  height: 40px; 
  grid-column-gap: auto; 
  -webkit-column-gap: auto; 
          column-gap: auto; 
  align-items:center;
}


.App #App-header #left-grid {
  grid-column: 1/2;
  display: grid;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  height: 100%;
  grid-column-gap: 10px;
  -webkit-column-gap: 10px;
          column-gap: 10px;
  grid-template-columns: auto auto auto;  
  align-items: center; 
  justify-self: flex-start;
}


.App #App-header #right-grid {
  grid-column: 2/2;
  display: grid;
  grid-template-columns: auto auto;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  height: 100%;
  align-items: center; 
  grid-column-gap: 10px; 
  -webkit-column-gap: 10px; 
          column-gap: 10px;
  justify-self: flex-end;
}

.App #App-header  #header-seperator {
  min-width: 100%;
  height: 0.6px;
  display: none;
  grid-column:  1/3;
  background-color: hsl(220, 14%, 75%);
  align-self: flex-end;
}

#item-display-desktop {
  width: 100%;  
  place-items: center;
  height: 80vh;
  display: none;
  margin: 0;
  grid-template-columns: auto auto;
}

#summary-and-add-to-cart {
  width: 60%;
  justify-self: center;
  height: 80%;
  align-self: center;
  margin: 0;
  display: grid;
  grid-template-columns: auto;
  grid-template-rows: auto auto;
}


