/* desktop responsiveness*/
@media only screen and (min-width: 992px) {
  .App #App-header {
      margin-left:50px;
      margin-right:50px;
      height: 50px;
  }

  .App #App-header  #header-seperator {
      display: grid;
  }
  

  #item-display-mobile {
    display: none;
  } 

  #item-display-desktop {
    display: grid;
    justify-content: center;
  }
}



#cart-div {
    position: relative;
    width: auto;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    
}

#cart-div img {
    -webkit-filter: brightness(0%) opacity(0.6) ;
            filter: brightness(0%) opacity(0.6) ;
}

#cart-div img:active {
    -webkit-filter: brightness(0%) opacity(1);
            filter: brightness(0%) opacity(1)
}

#cart-item-no {
    bottom:80%;
    left:55%;
    position: absolute;  
    color:#fff;
    background-color: hsl(26, 100%, 55%);
    border-radius:50%;
    width: 1.5em;
    font-size: 0.6em;
    text-align: center;
    margin: 0px;
}


/* desktop media queries for responsiveness*/
@media only screen and (min-width: 992px) {
    #cart-div {
        display: none;
    }  
}

 
#cart-item-div {
    margin-left: 5%;
    margin-right: 5%;
    background-color: #fff;
    position: absolute !important;
    width: 90%;
    height: 90%;
    border-radius: 10px;
    margin-top: 10px;
    z-index: 3;
    display: none /* block*/;
    overflow-y: auto;
    overflow-x: hidden;
    box-sizing: border-box;
}

#cart-item-div li {
    list-style-type: none;
    margin: 5px;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: space-around;
    align-items: center;
}

#cart-item-div li p {
    display: inline;
    font-size: 0.9em;
    margin: 0px;
}

#cart-item-div li .product-name {
    display: block;
}

#cart-item-div li  .resulting-amount {
        color: #000;
}
#cart-item-div li  .product-img {
    border-radius: 5px;
    margin: 0px;
    
}

#cart-item-div #cart-item-header {
    width: 100%;
    border-bottom: thin solid hsl(220, 14%, 75%);
    display: grid;
    height: 50px;
    font-size: 1em;
    align-items: center;
    margin: 0px;
    padding-left: 10px;

}

#cart-item-div #checkout-btn {
    display: grid;
    place-items: center;
    width: 90%;
    margin: auto;
    margin-top: 20px;
    margin-bottom: 10px;
    border-radius: 10px;
    color: #fff;
    font-weight: bold;
    cursor:pointer;
    height: 50px;
    background-color: hsl(26, 100%, 55%);
}

#cart-item-div  #empty-session-message {
    font-weight: 700;
    margin-top: 20px;
    margin-bottom: 20px;
    text-align: center;
    display: block;
} 

a {
    text-decoration: none;
}

 
#profile-div, #profile-div img {
    margin:0;
}

#profile-div {
    width: 30px;
    height: 30px;
    border-radius: 50%;
    display: grid;
    place-content: center;
}

/*Beginning of desktop responsiveness design*/
@media only screen and (min-width: 992px) {
    #profile-div:hover {
        border:2px solid  hsl(26, 100%, 55%);        
    }   

     #profile-div img {
        margin:0;
        width: 100%;
        height: 100%;
    }
    
}

/* style sheet for the navigation menu component*/
.nav-bar{
    margin:0;    
    width: auto;
    height: auto;
    display: inline-block;  
}


.nav-bar #nav-icon {
    display: block;
    height: 100%;
}


.nav-bar #nav-icon img {
    margin:0;
}

@-webkit-keyframes side-nav-anim-out {
    from {width: 0%;}
    to {width: 70%;}
}

@keyframes side-nav-anim-out {
    from {width: 0%;}
    to {width: 70%;}
}


@-webkit-keyframes side-nav-anim-in {
    from {width: 70%;}
    to {width: 0%;}
}


@keyframes side-nav-anim-in {
    from {width: 70%;}
    to {width: 0%;}
}

.side-nav {
    background-color:  #fff;
    width:0%;
    display: none;
    -webkit-animation: side-nav-anim-out 0.5s ;
            animation: side-nav-anim-out 0.5s ;
    height:100vh !important;
    z-index: 10;
    position: fixed;
    margin:0px;
    padding: 10px;  
    opacity: 1 !important;
          
}


@-webkit-keyframes side-nav-a-anim-out {
    from {margin-left: 0px;}
    to {margin-left: 15px;}
}


@keyframes side-nav-a-anim-out {
    from {margin-left: 0px;}
    to {margin-left: 15px;}
}

@-webkit-keyframes side-nav-a-anim-in {
    from {margin-left: 15px;}
    to {margin-left: 0px;}
}

@keyframes side-nav-a-anim-in {
    from {margin-left: 15px;}
    to {margin-left: 0px;}
}

.side-nav a {  
    -webkit-animation: side-nav-a-anim-out  0.5s ;  
            animation: side-nav-a-anim-out  0.5s ;  
    text-align: left;
    margin :15px;     
    display: block;
    text-decoration: none;
    color:#000

}

.side-nav #hide-nav {
    margin: 0;
    margin-left: 15px;
    margin-bottom: 20px;    
}


#disabler {    
    background-color: #000;
    width: 30%;
    height: 100vh;

}

